<template>
    <header>
      <ul>
        <li><router-link to="/">首页</router-link></li>
        <li><router-link to="/man">男子</router-link></li>
        <li><router-link to="/woman">女子</router-link></li>
      </ul>
    </header>
</template>

<script>
    export default {
      name: "app-header",
      data(){
          return {
            title:'Vue.js Demo'
          }
      }
    }
</script>

<style scoped>
header{
  padding: 20px;
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
}
  h1{
    text-align: center;
  }
  ul{
    display: flex;
    justify-content:center;
  }
  li{
    list-style: none;
    padding: 0 20px;

  }
  a{
    text-decoration:none;
    color: #000000;
  }

</style>



