<template>
  <div id="home">
    <app-header></app-header>
    <users v-bind:shops="manusers"></users>
    <app-footer></app-footer>
  </div>
</template>

<script>
  import Users from './Users'
  import Header from './Header'
  import Footer from './Footer'
    export default {
        name: "man",
      components: {
        "app-header": Header,
        "users": Users,
        "app-footer": Footer,
      },
      data(){
        return {
          users: [],
          manusers: []
        }
      },
      created() {
        axios.get('https://kuangkuang520.github.io/data/1.json').then(response=>{
          this.users=response.data;
          this.fliter()
        })
      },
      methods:{
          fliter:function () {
            for (var i=0;i<this.users.length;i++)
            { if(this.users[i].type=='男'){
              this.manusers.push(this.users[i]);
            }
            }
          }
      }
    }

</script>

<style scoped>

</style>



