<!--模板：html-->
<template>
  <div id="home">
    <app-header></app-header>
    <users v-bind:shops="users"></users>
    <app-footer></app-footer>
  </div>
</template>
<!--行为：处理逻辑-->
<script>
  // 局部组件
  import Users from './Users'
  import Header from './Header'
  import Footer from './Footer'

  export default {
    name: 'home',
    data() {
      return {
        users: []
      }
    },
    components: {
      "app-header": Header,
      "users": Users,
      "app-footer": Footer,
    },
    created() {
      axios.get('https://kuangkuang520.github.io/data/1.json').then(response=>{
        this.users=response.data;
      })
    }

  }
</script>
<!--样式：CSS-->
<style scoped>
  h1 {
    color: purple;
  }
</style>



